$(".order-form").submit(function (e) {
    e.preventDefault()
    let phoneIsValid = false
    let emailIsValid = false


    let phone = $("input[name='phone']")
    let email = $("input[name='email']")
    let pictureName = $("input[name='pictureName']")

    if ($(phone).intlTelInput("isValidNumber")) {
        phoneIsValid = true
        $(phone).parents(".fg").removeClass("_error")
    } else {
        phoneIsValid = false
        $(phone).parents(".fg").addClass("_error")
    }

    let emailPattern =  /^[^ ]+@[^ ]+\.[a-z]{2,3}$/
    let fieldVal = $(email).val()


    if(fieldVal.match(emailPattern) && fieldVal){
        $(email).parents(".fg").removeClass("_error")
        emailIsValid = true
    }
    else{
        $(email).parents(".fg").addClass("_error")
        emailIsValid = false
    }

    if(!phoneIsValid || !emailIsValid){
        return
    }


    let formData = {
        phone: phone.val(),
        email: email.val(),
        pictureName: pictureName.val()
    }

    $(".order-modal").addClass("_success")
    console.log(formData)

    $.ajax({
        type: "POST",
        utl: "php/send.php",
        data: formData,
        success: function success() {

        }
    })
})


function emailFieldValidate(field) {
    let emailPattern =  /^[^ ]+@[^ ]+\.[a-z]{2,3}$/
    let fieldVal = $(field).val()


    if(fieldVal.match(emailPattern) && fieldVal){
        $(field).parents(".fg").removeClass("_error")
        $(field).parents(".fg").find(".fg__error").html('')
        return true
    }
    else{
        $(field).parents(".fg").addClass("_error")
        $(field).parents(".fg").find(".fg__error").html('Email is not valid')
        return false
    }
}
